@import "../index";

.moves-title {
    padding: 9px;
    border-radius: 12px;
    border: 1px solid var(--borderColor2);
    background-color: var(--buttonsBackground);
}

.charged-attacks {
    margin-top: 24px;
}

.moves-list {
    list-style: none;
    padding: 0;
    margin: 0;
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 9px;
    gap: 9px;
}

.move-card {
    color: var(--titleMoveTextColor);
    border-radius: 12px;
}

.move-card-content {
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    text-align: left;
    gap: 3px 9px;
    padding: 2px;
}

.move-detail {
    border-radius: 12px;
    background-color: rgba(0, 0, 0, .3);
    box-shadow: 0 0 2px hsla(0,21%,90%,.158),0 12px 11px #00000009;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 6px;
}

.move-name {
    margin-right: auto;
    padding-right: 12px;
    padding: 6px;
}

.move-stats {
    gap: 6px;
    padding: 6px;
}

.move-stats-content {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 3px;
}

